export default {
    methods:{
        getStateNameByStateCode(e){
            return this.getStateCodes[e];
        },
    },
    computed:{
        getStateData() {
            return this.$store.getters.getData;
        },
        getStateCodes() {
            return this.$store.getters.getStateCode;
        },
        getStateMergedDetails(){
            return this.$store.getters.getMergedDetails;

        }
    }
}