const sortDate=(datesArr)=>{
    datesArr.sort((d1,d2)=>{
        const diff=d2.date.getTime()-d1.date.getTime();
        return diff;
    });
    let newDate={};
    for(let currentData of datesArr){
        let currentDate=currentData.date.toLocaleDateString();
        newDate[currentDate]=currentData;
    }
    return newDate;
};


export default {
    addStateNameToAllStateWiseData(state){
        let stateCodes=state.stateCodes;
        for(let k in state.data){
            if(stateCodes[k]){
                state.data[k].stateName=stateCodes[k];
            }else{
                delete state.data[k];
            }
        }
    },
    formatSateWiseData(state){
        let mainData=JSON.parse(JSON.stringify(state.data));
        // eslint-disable-next-line no-unused-vars
        let stateWiseData=state.stateWise;
        // eslint-disable-next-line no-unused-vars
        let stateCodes=state.stateCodes;
        let newData={};
        // eslint-disable-next-line no-unused-vars
        let total=null;
        // eslint-disable-next-line no-unused-vars
        for(let stateCode in mainData){
            let currentData=mainData[stateCode];
            const toPush=currentData;
            toPush.stateName=stateCodes[stateCode];
            toPush.dateWise=stateWiseData[stateCode].dates;
            toPush.stateCode=stateCode;
            for(let date in stateWiseData[stateCode].dates){
                toPush.dateWise[date].date=new Date(date);
            }

            toPush.dateWise= sortDate(Object.values(toPush.dateWise));

            if(stateCode.toLowerCase()==="tt"){
                total=toPush;
            }else{
                newData[stateCode]=toPush;

            }
            // console.log(total)
            // console.log(toPush);
        }
        state.mergedDetails={
            stateWise:newData,
            total:total
        };
    },
    sortDate(state,stateCode){
        let datesArr=Object.values(this.state.mergedDetails.stateWise[stateCode].dateWise);
        datesArr.sort((d1,d2)=>{
            const diff=d2.date.getTime()-d1.date.getTime();
            return diff;
        });
        state.mergedDetails.stateWise[stateCode].dateWise={};
        for(let currentData of datesArr){
            let currentDate=currentData.date.toLocaleDateString();
            state.mergedDetails.stateWise[stateCode].dateWise[currentDate]=currentData;
        }
        state.mergedDetails.stateWise[stateCode].dateSortedAt=new Date().getTime();
        // console.log(Object.keys(  state.mergedDetails.stateWise[stateCode].dateWise))
    }
}