<template>
    <div>
        <table class="w-full relative bg-white">
            <thead class="sticky top-0">
            <tr>
                <th rowspan="2">Date</th>
                <th :colspan="subLabels.length">Delta</th>
                <th :colspan="subLabels.length">Delta7</th>
                <th :colspan="subLabels.length">Total</th>
            </tr>

            <tr class="sticky top-0">
                <th v-for="(i,key) in subLabels" :key="'0'+key" class="capitalize">{{i}}</th>
                <th v-for="(i,key) in subLabels" :key="'1'+key" class="capitalize">{{i}}</th>
                <th v-for="(i,key) in subLabels" :key="'2'+key" class="capitalize">{{i}}</th>
            </tr>

            </thead>
            <tbody>
            <tr v-for="(i,key) in getCurrentTableData" :key="key">
                <td v-for="(j,k) in i" :key="k">{{j}}</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>


<script>
    import stateGetters from "@/mixins/stateGetters";

    export default {
        name: "TableViewDateWiseState",
        mixins: [stateGetters],
        props: {
            subLabels: {
                type: Array[String],
                required: true
            },
            selectedYear: {
                type: Number,
                required: true
            },
            selectedMonth: {
                type: Number,
                required: true
            },
            stateCode: {
                type: String,
                required: true
            }

        },
        computed: {
            getCurrentTableData() {
                const currentStateDetails = JSON.parse(JSON.stringify(this.getStateMergedDetails.stateWise[this.stateCode] || {}));

                if (!currentStateDetails) {
                    return [[]];
                }

                const comparator = (d1, d2) => {
                    const diff = new Date(d1.date).getTime() - new Date(d2.date).getTime();
                    return diff;
                };
                let currentArr = Object.values(currentStateDetails.dateWise) || [];
                currentArr = currentArr.filter((el) => {
                    el.date=new Date(el.date);
                    if(this.selectedYear<0){
                        return true;
                    }
                    if(this.selectedMonth<0){
                        return el.date.getFullYear() === this.selectedYear;
                    }
                    return el.date.getFullYear() === this.selectedYear && el.date.getMonth() === this.selectedMonth%12;
                });
                currentArr.sort((d1, d2) => {
                    return comparator(d1, d2)
                });


                const result = [];
                for (let data of currentArr) {
                    const pushData = [new Date(data.date).toDateString()];

                    for (let subLabel of this.subLabels) {
                        if (data.delta) {
                            pushData.push(data.delta[subLabel] || 0)
                        } else {
                            pushData.push(0)
                        }
                    }

                    for (let subLabel of this.subLabels) {
                        if (data.delta) {
                            pushData.push(data.delta7[subLabel] || 0)
                        } else {
                            pushData.push(0)
                        }
                    }

                    for (let subLabel of this.subLabels) {
                        if (data.delta) {
                            pushData.push(data.total[subLabel] || 0)
                        } else {
                            pushData.push(0)
                        }
                    }
                    result.push(pushData)
                }
                return result;

            }
        }
    }
</script>

<style scoped>

    td, th {
        border: 2px solid gray;
        height: 2.3rem;
    }
</style>