<template>
    <div>
        <div id="chartContainer">
            <canvas class="cursor-pointer" ref="covid_chart"></canvas>
        </div>
    </div>
</template>

<script>
    import stateGetters from "@/mixins/stateGetters";
    import Chart from "chart.js/auto";

    export default {
        name: 'Chart',
        mixins: [stateGetters],
        props: {
            type: {
                type: String,
                default: () => {
                    return 'bar'
                }
            },
            data: {
                type: String,
                default: () => {
                    return 'state'
                }
            },
            stateCode: {
                type: String,
                default: "AS"
            },
            year:{
                type:Number,
                default:-1
            },
            month:{
                type:Number,
                default:-1
            }
        },
        data() {
            return {
                chartType: this.type,
                ChartData: null,
                dataType: this.data,
                currentChart:null,
            }
        },
        mounted() {
            this.setChart()
        },
        methods: {
            getChartDataOfDateForAllStates() {
                const dataInput = JSON.parse(JSON.stringify(this.getStateMergedDetails.stateWise));
                const labels = Object.keys(dataInput);

                const confirmedCases = [];
                const deceasedCases = [];
                const recoveredCases = [];
                const testedCases = [];

                for (let i in dataInput) {
                    let value = dataInput[i];
                    confirmedCases.push(value.total.confirmed);
                    deceasedCases.push(value.total.deceased);
                    recoveredCases.push(value.total.recovered);
                    testedCases.push(value.total.tested);
                }

                return {
                    labels,
                    confirmedCases,
                    recoveredCases,
                    testedCases,
                    deceasedCases
                }
            },
            getChartDataForOneStates() {
                let dataInput = Object.values(this.getStateMergedDetails.stateWise[this.stateCode].dateWise);
                dataInput.sort((el1,el2)=>{
                    return el1.date.getTime()-el2.date.getTime();
                });
                dataInput=dataInput.filter((el)=>{
                    // console.log(el);
                    if(this.year<0){
                        return true;
                    }
                    if(this.month<0){

                        return el.date.getFullYear()==this.year;
                    }
                    return el.date.getMonth()==this.month && el.date.getFullYear()==this.year;
                });
                const labels = [];
                const confirmedCases = [];
                const deceasedCases = [];
                const recoveredCases = [];
                const testedCases = [];

                for (let value of dataInput) {
                    labels.push(value.date);
                    confirmedCases.push(value.total.confirmed || 0);
                    deceasedCases.push(value.total.deceased || 0);
                    recoveredCases.push(value.total.recovered || 0);
                    testedCases.push(value.total.tested || 0);
                }
                return {
                    labels:labels.map(el=>{
                        return el.toDateString();
                    }),
                    confirmedCases,
                    recoveredCases,
                    testedCases,
                    deceasedCases
                }

            },
            getChartConfiguration(chartType, labels, confirmedCases, deceasedCases, recoveredCases){
                return  {
                    type: chartType,
                    data: {
                        labels: labels,
                        datasets: [
                            {
                                label: "Confirmed Cases",
                                data: confirmedCases,
                                backgroundColor: "rgba(0,156,255,0.8)",
                                borderColor: "rgba(0,156,255,1)",
                                borderWidth: 3,
                                pointRadius:0,
                            },
                            {
                                label: "Deceased Cases",
                                data: deceasedCases,
                                backgroundColor: "rgba(255,0,0,0.7)",
                                borderColor: "rgba(255,0,0,1)",
                                borderWidth: 3,
                                pointRadius:0,
                            },
                            {
                                label: "Recovered Cases",
                                data: recoveredCases,
                                backgroundColor: "rgba(71, 183,132,.5)",
                                borderColor: "#47b784",
                                borderWidth: 3,
                                pointRadius:0,
                            }
                        ],
                    },
                    options: {
                        tooltips: {
                            mode: 'index',
                            intersect: false
                        },
                        hover: {
                            mode: 'index',
                            intersect: false
                        },
                        responsive: true,
                        lineTension: 1,
                        scales: {
                            yAxes: [
                                {
                                    ticks: {
                                        beginAtZero: true,
                                        padding: 55,
                                    },
                                },
                            ],
                        },
                    },
                };
            },
            setChart(){
                const ctx = this.$refs.covid_chart;
                let data;
                if(this.dataType==="state"){
                    data=this.getChartDataForOneStates();
                }else if(this.dataType==="date"){
                    data=this.getChartDataOfDateForAllStates();
                }
                this.currentChart=new Chart(ctx,this.getChartConfiguration(this.type,data.labels,data.confirmedCases,data.deceasedCases,data.recoveredCases));
            }
        },
        watch:{
            month(){
                this.currentChart.destroy();
                this.setChart();
            },
            year(){
                this.currentChart.destroy();
                this.setChart();
            }
        }
    }
</script>

<style scoped>
    #chartContainer {
        margin: 0 10%;
    }
</style>