export  default {
    methods:{
        formatDataForTabularView(keysToFetch=[],data,callBack=defaultComparator){
            if(!Array.isArray(keysToFetch)){
               throw new Error("keys should be in array")
            }
            let keys1=Object.keys(data);
            let result=[];
            for(let key of keys1){
                const newData=[];
                const currentData=data[key];
                for(let k of keysToFetch){
                    const data=callBack(currentData,k);
                    // console.log(data,k);
                    newData.push(data)
                }
                result.push(newData);
            }
            return result;
        },
        formatDataForTabularViewForDateWise(dateWiseData,fieldsToShowInTable){
            const result=[];
            for (let i of dateWiseData){
                console.log(i);
                let newData=[];
                for(let j of fieldsToShowInTable.slice(1)){
                    let confirmedCases=0;
                    let deceasedCases=0;
                    let recovered=0;
                    if(dateWiseData[i] && dateWiseData[i][j]){
                        // console.log(i,Object.keys(dateWiseData[i][j]));
                        confirmedCases=dateWiseData[i][j].confirmed|| confirmedCases;
                        deceasedCases=dateWiseData[i][j].deceased||deceasedCases;
                        recovered=dateWiseData[i][j].confirmed || recovered;
                    }
                    newData.push(confirmedCases);
                }
                result.push(newData);
            }
            return result;
        }
    }

}