<template>
  <div class="home">
    <tabular :thead="thead" :tbody="tbody" @click="clickedOnTable"/>
  </div>
</template>

<script>
  // @ is an alias to /src
  import Tabular from "@/components/Layouts/Tabular";
  import stateGetters from "@/mixins/stateGetters";
  import tableDataFormatter from "@/mixins/tableDataFormatter";

  export default {
    name: 'Home',
    mixins:[stateGetters,tableDataFormatter],
    data:()=>({
      thead:[],
      tbody:[],
      labels:{
        "state name":"stateName",
        "confirmed":"confirmed",
        "deceased":"deceased",
        "recovered":"recovered",
        "tested":"tested",
        "vaccinated 1":"vaccinated1",
        "vaccinated 2":"vaccinated2"

      }
    }),
    components: {
      Tabular,
    },
    methods:{
      clickedOnTable(e){
        this.$router.push("state/"+Object.keys(this.getStateData)[e]);
      }
    },
    beforeMount(){
      this.thead =Object.keys(this.labels);
      const dataKeys=Object.values(this.labels);
      let comparator=(currentData,k)=>{
        if(k==="stateName"){
          return  currentData.stateName;
        }else{
          return  currentData.total[k];
        }
      };
      this.tbody=this.formatDataForTabularView(dataKeys,this.getStateMergedDetails.stateWise,comparator);

    }
  }
</script>
