import Vue from 'vue'
import App from './App.vue'
import "@/assets/styles/main.css";
import router from './router'
import store from "./store/index";
import vuetify from './plugins/vuetify'
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app');
