<template>
    <table class="w-full box-border pl-3 border-separate cursor-pointer">
        <thead>
        <tr>
            <th class="text-lg  text-orange-600 capitalize border-green-normal h-10 bg-green" v-for="(i,key) in thead" :key="key">{{i}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(i,key) in tbody"
            :key="key"
            class="hover:bg-green-normal  hover:text-white"
            @click="$emit('click',key)"
        >
            <td class="text-left" v-for="(j,k) in i"  :key="k">{{j}}</td>
        </tr>
        </tbody>
    </table>
</template>

<script>
    import stateNameFromStateCode from "@/mixins/stateGetters";

    export default {
        name: "Tabular",
        mixins:[stateNameFromStateCode],
        props:{
            tbody:Array,
            thead:Array,
        }

    }
</script>

<style scoped>
    table{
        padding: 1em;
    }
    td,th{
        border: 2px solid green;
        height: 2.5em;
    }
</style>