<template>
    <div class="min-h-screen" data-app>

        <v-container>
            <v-card
                    elevation="2"
                    color="blue darken-3"
            >
                <v-card-title>
                    {{stateName}}
                </v-card-title>
                <v-card-text>
                    <v-row>
                        <v-select
                                :items="availableYear"
                                dense
                                v-model="selectedYear"
                                outlined
                        >
                        </v-select>
                        <v-spacer></v-spacer>
                        <v-select
                                :items="availableMonth"
                                dense
                                v-model="selectedMonth"
                                outlined
                        >
                        </v-select>
                        <v-spacer></v-spacer>
                        <v-select
                                :items="availableUI"
                                dense
                                v-model="selectedUI"
                                outlined
                        >
                        </v-select>
                    </v-row>
                </v-card-text>

            </v-card>
            <div class="box-border py-4"></div>
            <div v-if="selectedUI.toLowerCase()==='table'">
                <table-view-date-wise-state
                        :subLabels="subLabels"
                        :selectedYear="getYear"
                        :selectedMonth="getSelectedMonth"
                        :stateCode="$route.params.stateCode"
                />
            </div>
            <div v-else-if="selectedUI.toLowerCase()==='chart'">
                <chart
                        :year="getYear"
                        :month="getSelectedMonth"
                        :type="'line'"
                        :stateCode="$route.params.stateCode"/>
            </div>
        </v-container>
    </div>
</template>

<script>
    import stateNameFromStateCode from "@/mixins/stateGetters";
    import tableDataFormatter from "@/mixins/tableDataFormatter";
    import TableViewDateWiseState from "@/components/UI/TableViewDateWiseState";
    import Chart from "@/components/UI/Chart";

    export default {
        name: "StateCovidFullDetails",
        components: {Chart, TableViewDateWiseState},
        mixins: [stateNameFromStateCode, tableDataFormatter],
        data: () => ({
            availableUI: [
                "Table",
                "Chart"
            ],
            selectedUI: null,
            stateName: undefined,
            fieldsToShowInTable: ['date', 'delta', 'delta7', 'total'],
            dataForTable: [],
            subLabels: [
                // "tested",
                "confirmed",
                "recovered",
                "deceased",
            ],
            selectedMonth: "January",
            selectedYear: 2022,
            availableMonth: [
                "All",
                "January",
                "February",
                "March",
                "April",
                "May",
                "June",
                "July",
                "August",
                "September",
                "October",
                "November",
                "December"
            ],
            availableYear: ["All", 2020, 2021, 2022]
        }),
        beforeMount() {
            this.stateName = this.getStateNameByStateCode(this.$route.params.stateCode);
            this.dataForTable = [];
            this.selectedUI = this.availableUI[0];
            this.dataForTable = Object.values(this.getStateMergedDetails.stateWise[this.$route.params.stateCode].dateWise);
        },
        computed: {
            getSelectedMonth: {
                set(e) {
                    this.selectedMonth = e;
                    return e;
                },
                get() {
                    return this.availableMonth.indexOf(this.selectedMonth) - 1
                }

            },
            getYear() {
                if (isNaN(this.selectedYear)) {
                    return -1;
                }
                return this.selectedYear;
            }
        },
    }
</script>

